import { Button, Cell, DropdownMenu, Switch } from "@taroify/core"
import { View, PageMeta } from "@tarojs/components"
import { Key, useCallback, useState } from "react"
import Block from "../../../components/block"
import Page from "../../../components/page"

import "./index.scss"

function BasicDropdownMenu() {
  const [options] = useState([
    { title: "", value: 0 },
    { title: "", value: 1 },
    { title: "", value: 2 },
  ].concat(Array.from({ length: 20 }, (_, i) => ({ title: "" + i, value: 3 + i }))))
  const [opened, setOpened] = useState(false)
  const getScrollHeight = useCallback((len) => {
    return len < 5 ? len * 46 : 5 * 46
  }, [])
  return (
    <>
      <PageMeta pageStyle={opened ? "overflow: hidden;" : ""} />
      <DropdownMenu>
        {/* @ts-ignore */}
        <DropdownMenu.Item style={{ "--dropdown-menu-item-content-max-height": getScrollHeight(23) + "px" }} options={options} lock onOpen={() => setOpened(true)} onClose={() => setOpened(false)} />
         {/* @ts-ignore */}
        <DropdownMenu.Item style={{ "--dropdown-menu-item-content-max-height": getScrollHeight(4) + "px" }} lock onOpen={() => setOpened(true)} onClose={() => setOpened(false)}>
          <DropdownMenu.Option value={0}></DropdownMenu.Option>
          <DropdownMenu.Option value={1}></DropdownMenu.Option>
          <DropdownMenu.Option value={2}></DropdownMenu.Option>
          <DropdownMenu.Option value={3}>2</DropdownMenu.Option>
        </DropdownMenu.Item>
      </DropdownMenu>
    </>
  )
}

function DropdownMenuWithCustomContent() {
  const [value, setValue] = useState<Key | false>()
  const [option1, setOption1] = useState(0)
  const [switch1, setSwitch1] = useState(true)
  const [switch2, setSwitch2] = useState(false)
  const [options] = useState([
    { title: <View><View></View></View>, value: 0 },
    { title: "", value: 1 },
    { title: "", value: 2 },
  ])
  return (
    <DropdownMenu value={value} onChange={setValue}>
      <DropdownMenu.Item value={option1} onChange={setOption1} options={options}></DropdownMenu.Item>
      <DropdownMenu.Item title="">
        <Cell title="" align="center">
          <Switch size="24" checked={switch1} onChange={setSwitch1} />
        </Cell>
        <Cell title="" align="center">
          <Switch size="24" checked={switch2} onChange={setSwitch2} />
        </Cell>
        <View style="padding: 5px 16px;">
          <Button color="primary" block shape="round" onClick={() => setValue(false)}>
            
          </Button>
        </View>
      </DropdownMenu.Item>
    </DropdownMenu>
  )
}

function DropdownMenuWithCustomColor() {
  return (
    <DropdownMenu className="custom-color">
      <DropdownMenu.Item>
        <DropdownMenu.Option value={0}></DropdownMenu.Option>
        <DropdownMenu.Option value={1}></DropdownMenu.Option>
        <DropdownMenu.Option value={2}></DropdownMenu.Option>
      </DropdownMenu.Item>
      <DropdownMenu.Item>
        <DropdownMenu.Option value={0}></DropdownMenu.Option>
        <DropdownMenu.Option value={1}></DropdownMenu.Option>
        <DropdownMenu.Option value={2}></DropdownMenu.Option>
      </DropdownMenu.Item>
    </DropdownMenu>
  )
}

function UpDropdownMenu() {
  return (
    <DropdownMenu direction="up">
      <DropdownMenu.Item>
        <DropdownMenu.Option value={0}></DropdownMenu.Option>
        <DropdownMenu.Option value={1}></DropdownMenu.Option>
        <DropdownMenu.Option value={2}></DropdownMenu.Option>
      </DropdownMenu.Item>
      <DropdownMenu.Item>
        <DropdownMenu.Option value={0}></DropdownMenu.Option>
        <DropdownMenu.Option value={1}></DropdownMenu.Option>
        <DropdownMenu.Option value={2}></DropdownMenu.Option>
      </DropdownMenu.Item>
    </DropdownMenu>
  )
}

function DisabledDropdownMenu() {
  return (
    <DropdownMenu>
      <DropdownMenu.Item disabled>
        <DropdownMenu.Option value={0}></DropdownMenu.Option>
        <DropdownMenu.Option value={1}></DropdownMenu.Option>
        <DropdownMenu.Option value={2}></DropdownMenu.Option>
      </DropdownMenu.Item>
      <DropdownMenu.Item disabled>
        <DropdownMenu.Option value={0}></DropdownMenu.Option>
        <DropdownMenu.Option value={1}></DropdownMenu.Option>
        <DropdownMenu.Option value={2}></DropdownMenu.Option>
      </DropdownMenu.Item>
    </DropdownMenu>
  )
}

export default function DropdownMenuDemo() {
  return (
    <Page className="dropdown-menu-demo" title="DropdownMenu ">
      <Block title="">
        <BasicDropdownMenu />
      </Block>
      <Block title="">
        <DropdownMenuWithCustomContent />
      </Block>
      <Block title="">
        <DropdownMenuWithCustomColor />
      </Block>
      <Block title="">
        <UpDropdownMenu />
      </Block>
      <Block title="">
        <DisabledDropdownMenu />
      </Block>
      <View style={{ height: "400px", background: "transparent", width: "100vw" }}></View>
    </Page>
  )
}
